@use '../utils' as *;

//Edytować poniżej

body {
  min-width: 320px;
  font-family: 'Montserrat', sans-serif;
}
.over {
  width: 100%;
  background-color: $primary-text;
  height: fit-content;
}
@media (min-width: 768px) {
  body {
    min-width: 768px;
  }
}
@media (min-width: 1200px) {
  body {
    min-width: 1200px;
  }
}
